import React, { useState } from 'react';
import { Tooltip, Button} from '@carbon/react';
import { ThumbsUp, ThumbsUpFilled, ThumbsDown, ThumbsDownFilled, Copy, Checkmark } from '@carbon/icons-react';
import './Feedback.scss';

function Feedback({ responseMsg }) {

  const [like, setLike] = useState(false);
  const [dislike, setDislike] = useState(false);
  const [copied, setCopied] = useState(false);

  function onLikeClick() {
    setLike(!like);
    if(!like==true && dislike==true) {
      setDislike(!dislike);
    }
  }

  function onDislikeClick() {
    setDislike(!dislike);
    if(!dislike==true && like==true) {
      setLike(!like);
    }
  }

  function copyToClipboard() {
    const text = responseMsg;
    if (typeof text === 'string' && text.trim().length > 0) {
      // Check the browser support clipboard or not
      if (navigator.clipboard) {
        navigator.clipboard.writeText(text)
          .then(() => {
            setCopied(true);
            // Set copy status back
            setTimeout(() => setCopied(false), 3000);
          })
          .catch(err => {
            console.error('[WXA4Z] Failed to copy text: ', err);
          });
      } else {
        console.error('[WXA4Z] Cannot copy text in this browser!')
        // Fallback method for older browsers
        // const textarea = document.createElement('textarea');
        // textarea.value = text;
        // document.body.appendChild(textarea);
        // textarea.select();
        // try {
        //   document.execCommand('copy');
        //   alert('Text copied to clipboard');
        // } catch (err) {
        //   console.error('Failed to copy text: ', err);
        //   alert('Failed to copy text');
        // }
        // document.body.removeChild(textarea);
      }
    }
  };
   
  return (
    <div className="MyCustomFeedback" >
      <span>
        <Tooltip align="bottom" label="Copy to clipboard">
          <Button kind="ghost" onClick={copyToClipboard}>
            {!copied && <Copy />}
            {copied && <Checkmark />}
          </Button>
        </Tooltip>
      </span>
      <span>
        <Tooltip align="bottom" label="I like this response">
          <Button kind="ghost" onClick={onLikeClick}>
            {!like && <ThumbsUp />}
            {like && <ThumbsUpFilled />}
          </Button>
        </Tooltip>
      </span>
      <span>
        <Tooltip align="bottom" label="I dislike this response">
          <Button kind="ghost" onClick={onDislikeClick}>
            {!dislike && <ThumbsDown />}
            {dislike && <ThumbsDownFilled />}
          </Button>
        </Tooltip>
      </span>
    </div>
  );
}

export { Feedback };
