import React, { useCallback } from 'react';
import { createRoot } from 'react-dom/client';
import { WebChatCustomElement, setEnableDebug } from '@ibm-watson/assistant-web-chat-react';
import { Feedback } from './Feedback';
import './WebChat.scss';

// Include this if you want to get debugging information from this library. Note this is different than
// the web chat "debug: true" configuration option which enables debugging within web chat.
setEnableDebug(true);

function WebChat({ config, changeConfig, changeLoading }) {

  let messages = [];
  let agentMessages = [];

  function onSend(event) {
    saveMessage(event);
  }

  function onReceive(event) {
    saveMessage(event);
    addFeedbackIcons();
  }

  function saveMessage(event) {
    messages.push(event.data);
  }
  
  function onAgentSend(event) {
    saveAgentMessage(event);
  }

  function onAgentReceive(event) {
    saveAgentMessage(event);
  }

  function saveAgentMessage(event) {
    agentMessages.push({
      ...event.data,
      // Add the agent's nickname to this object to make it easier to process both the bot and agent messages.
      agentNickname: (event.agentProfile && event.agentProfile.nickname) || 'Agent',
    });
  }

  function onHistoryBegin(event) {
    saveHistory(event);
  }
  
  function saveHistory(event) {
    messages.push(...event.messages);
  }

  function addFeedbackIcons() {
    const lastResponse = document.getElementsByClassName("WAC__message WAC__message--lastMessage WAC__message--response");
    if (lastResponse && lastResponse[0]) {
      const innerMsg = lastResponse[0].getElementsByClassName("WAC__received--inner");
      if (innerMsg && innerMsg[0]) {
        innerMsg[0].appendChild(document.createElement('div'));
        const responseMsg = removeAIWarningEnding(innerMsg[0].innerText);
        createRoot(innerMsg[0].lastChild).render(<Feedback responseMsg={responseMsg} />);
      }
    }
  }

  function removeAIWarningEnding(str){
    if(typeof str === 'string' && str.trim().length > 0){
      const regex = /\n\s*\n\s*AI\s*Accuracy of generated answers may vary\.$/;
      const hasUnwantedEnding = regex.test(str);
      // Remove the ending if it exists
      const cleanedString = hasUnwantedEnding ? str.replace(regex, '') : str;
      return cleanedString;
    }
    return str;
  }

  function doDownload() {
    const downloadContent = createDownload();

    const blob = new Blob([downloadContent], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);

    // To automatically trigger a download, we have to create a fake "a" element and then click it.
    const timestamp = new Date().toISOString().replace(/[_:]/g, '-').replace(/.[0-9][0-9][0-9]Z/, '');
    const a = document.createElement('a');
    a.setAttribute('href', url);
    a.setAttribute('download', `Chat History ${timestamp}.csv`);
    a.click();
  }

  function createDownload() {
    let downloadLines = [];
    downloadLines.push('Assistant messages')
    downloadLines.push(createDownloadText('From', 'Message'));
    downloadLines = downloadLines.concat(messagesToLines(messages));

    if (agentMessages.length) {
      // If there are any messages with a human agent, add those to the export.
      downloadLines.push('');
      downloadLines.push('Human agent messages');
      downloadLines.push(createDownloadText('From', 'Message'));
      downloadLines = downloadLines.concat(messagesToLines(agentMessages));
    }

    return downloadLines.join('\n');
  }

  function messagesToLines(messages) {
    const downloadLines = [];

    messages.forEach(message => {
      if (message.input && message.input.text) {
        // This is a message that came from the user.
        downloadLines.push(createDownloadText('You', message.input.text));
      } else if (message.output && message.output.generic && message.output.generic.length) {
        // This is a message that came from the assistant or an agent. It can contain an array of individual message items.
        const systemName = message.agentNickname || 'Watsonx';
        message.output.generic.forEach(messageItem => {
          // This is only handling a text response but you can handle other types of responses here as well as
          // custom responses.
          if (messageItem && messageItem.text) {
            downloadLines.push(createDownloadText(systemName, messageItem.text));
          }
        });
      }
    });

    return downloadLines;
  }

  function createDownloadText(from, text) {
    return `${from},${escapeCSV(text)}`;
  }

  function escapeCSV(text) {
    // Remove any newline characters which aren't supported in all CSV formats.
    text = text.replace(/\n/g, ' ');

    // If the text contains a comma or a double quote, the entire thing needs to be surrounded by double quotes. If
    // the string contains a double quote, then each double quote needs to be replaced with two double quotes.
    if (text.match(/[,"]/)) {
      return `"${text.replace(/"/g, '""')}"`;
    }
    return text;
  }

  function doChangeConfig() {
    changeConfig();
  }

  const onBeforeRender = useCallback((instance) => {
    instance.on({ type: 'send', handler: onSend });
    instance.on({ type: 'receive', handler: onReceive });
    instance.on({ type: 'agent:send', handler: onAgentSend });
    instance.on({ type: 'agent:receive', handler: onAgentReceive });
    instance.on({ type: 'history:begin', handler: onHistoryBegin });

    instance.updateCustomMenuOptions([
      { text: 'Download transcript', handler: doDownload },
      { text: 'Reset web chat integration', handler: doChangeConfig }
    ]);
  });

  const onAfterRender = useCallback((instance) => {
    changeLoading(false);
  });

  const renderCustomResponse = useCallback((event) => {
  });

  const onViewChange = useCallback((event, instance) => {
    // instance.updateHomeScreenConfig({
    //   is_on: true,
    //   greeting: "Hi! I'm IBM Watsonz for Z Assistant. How can I help you today?",
    //   starters: {
    //     is_on: true,
    //     buttons: [
    //       { label: 'Sign in' },
    //       { label: 'Create an account' },
    //       { label: 'z/OSMF activities' },
    //       { label: 'z/OSMF user guide' }
    //     ]
    //   }
    // });
  });

  return (
    <WebChatCustomElement className="MyCustomElement"
      config={config}
      onBeforeRender={onBeforeRender}
      onAfterRender={onAfterRender}
      renderCustomResponse={renderCustomResponse}
      onViewChange={onViewChange} />
  );
}

export { WebChat };
