import React, { useState, useRef, useCallback, useEffect } from 'react';
import { ModalInput } from './ModalInput';
import { WebChat } from './WebChat';
import { Loading } from '@carbon/react';

function App() {

  const userInputOrignRef = useRef('');
  const [config, setConfig] = useState(null);
  const [launchInput, setLaunchInput] = useState(false);
  const [inputError, setInputError] = useState('');
  const [allowCancel, setAllowCancel] = useState(false);
  const [launchWebChat, setLaunchWebChat] = useState(false);
  const [loading, setLoading] = useState(false);

  const defaultConfig = {
    themeConfig: {
      carbonTheme: 'white',
      useAITheme: true
    },
    openChatByDefault: true,
    showLauncher: false,
    hideCloseButton: true,
    showRestartButton: true,
    debug: true,
    onError: (instance) => {
      console.error('[WXA4Z] Web chat load error: ' + instance.errorType + ': ' + instance.message);
      if (instance.errorType == "INITIAL_CONFIG") {
        setLaunchInput(true);
        setInputError(instance.message);
        setAllowCancel(false);
        setLaunchWebChat(false);
      }
      setLoading(false);
    }
  };

  const baseURL = window.location.origin + '/zosmf'; // 'https://localhost:4300/zosmf';
  const header = {
    // 'Authorization': 'Basic em9zbWZhZDp6b3NtZmFk', // for local debug only
    // 'X-CSRF-ZOSMF-HEADER': 'zosmf', // for local debug only
    // 'Access-Control-Allow-Origin': baseURL, // for local debug only
    // 'X-Frame-Options': baseURL, // for local debug only
    'Content-Type': 'application/json'
  };

  useEffect(() => {
    const getUserConfig = async () => {
      await fetch(baseURL + '/settings/app/com.ibm.zos.wxa4z/CHAT/webchat/configs/1?version=1.0.0', {
        method: 'GET',
        headers: header
      }).then(response => response.json())
        .then(data => {
          if (data && data.value) {
            console.log('[WXA4Z] Get user config from server: ', data.value);
            setConfig({
              ...defaultConfig,
              ...data.value.userConfig
            });
            userInputOrignRef.current = data.value.userConfigOrign;
            // User config is already done before, open web chat directly
            setLaunchInput(false);
            setLaunchWebChat(true);
            setLoading(true);
          } else if (data && data.errorData) {
            console.log('[WXA4Z] Failed to get user config from server: ', data.errorData[0].messageText);
            // Need user config first
            setLaunchInput(true);
            setInputError(data.errorData[0].messageText);
            setAllowCancel(false);
          } else {
            console.log('[WXA4Z] Failed to get user config from server: no data in response.');
            // Need user config first
            setLaunchInput(true);
            setInputError('');
            setAllowCancel(false);
          }
        })
        .catch(err => {
          console.log('[WXA4Z] Failed to get user config from server: ', err);
          // Need user config first
          setLaunchInput(true);
          setInputError(err.message);
          setAllowCancel(false);
        });
    };

    // re-style zosmf window iframe
    const iframe = window.frameElement; 
    if(iframe){
      iframe.setAttribute("style", "padding: 0 !important; height: calc(100% - 25px) !important;");
    }

    getUserConfig();
  }, []);

  const onStart = useCallback((userInputObj, userInputOrign) => {
    console.log('[WXA4Z] Start to set user config: ', userInputObj);

    const putUserConfig = async () => {
      await fetch(baseURL + '/settings/app/com.ibm.zos.wxa4z/CHAT/webchat/configs/1?version=1.0.0', {
        method: 'PUT',
        headers: header,
        body: JSON.stringify({
          'version': '1.0.0',
          'update': true,
          'value': {
            'userConfig': userInputObj,
            'userConfigOrign': userInputOrign
          }
       })
      }).then(response => response.json())
        .then(data => {
          if (data && data.value) {
            console.log('[WXA4Z] Put user config to server: ', data);
            setConfig({
              ...defaultConfig,
              ...data.value.userConfig
            });
            userInputOrignRef.current = data.value.userConfigOrign;
            // User config is done, relaunch a new web chat
            setLaunchInput(false);
            setLaunchWebChat(true);
            setLoading(true);
          } else if (data && data.errorData) {
            console.log('[WXA4Z] Failed to put user config to server: ', data.errorData[0].messageText);
            // Need user config again
            setInputError(data.errorData[0].messageText);
            setAllowCancel(true);
          } else {
            console.log('[WXA4Z] Failed to put user config to server: no data in response.');
            // Need user config again
            setInputError('');
            setAllowCancel(true);
          }
        })
        .catch(err => {
          console.log('[WXA4Z] Failed to put user config to server: ', err);
          // Need user config again
          setInputError(err.message);
          setAllowCancel(true);
        });
    };

    putUserConfig();
  });

  const onCancel = useCallback(() => {
    setLaunchInput(false);
  });

  const changeConfig = useCallback(() => {
    setLaunchInput(true);
    setInputError('');
    setAllowCancel(true);
  });

  const changeLoading = useCallback((isLoading) => {
    setLoading(isLoading);
  });

  return (
    <>
      <ModalInput
        launchInput={launchInput}
        lastInput={userInputOrignRef.current}
        inputError={inputError}
        allowCancel={allowCancel}
        onStart={onStart}
        onCancel={onCancel} />
      {loading && <Loading />}
      {launchWebChat && <WebChat
                          config={config}
                          changeConfig={changeConfig}
                          changeLoading={changeLoading} />}
    </>
  );
}

export default App;
