import React, { useState, useCallback, useEffect } from 'react';
import { Modal, TextArea, Link, InlineNotification } from '@carbon/react';
import './ModalInput.scss';

function ModalInput({ launchInput, lastInput, inputError, allowCancel, onStart, onCancel }) {

  const [userInputObj, setUserInputObj] = useState(null);
  const [userInputOrign, setUserInputOrign] = useState('');
  const [userInputValid, setUserInputValid] = useState(false);
  const [userInputInvalidText, setUserInputInvalidText] = useState('');

  const uuidRegexV4 = /^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$/i;
  // const regions = [
  //   'dev', 'aws-dev', 'staging', 'aws-staging', 'aws-us-east-1',
  //   'us-south', 'us-east', 'jp-tok', 'au-syd', 'eu-gb', 'eu-de', 'kr-seo'
  // ];

  useEffect(() => {
    setUserInputOrign(() => {
      const newUserInputOrign = lastInput;
      validateUserInput(newUserInputOrign);
      return newUserInputOrign;
    });
  }, [launchInput]);

  function validateUserInput(userInputOrign) {
    if (userInputOrign == '') {
      setUserInputValid(false);
      setUserInputInvalidText('');
    } else {
      const result = fetchCopyScriptPara(userInputOrign);
      if (result.invalid) {
        setUserInputValid(false);
        setUserInputInvalidText(result.invalidText);
      } else {
        setUserInputValid(true);
        setUserInputInvalidText('');
        setUserInputObj(result.scriptParms);
      }
    }
  };

  function fetchCopyScriptPara(scriptString) {
    let serversObj = {};
    let scriptParms = { "servers": serversObj };
    let result = { "invalid": true, "invalidText": "", "scriptParms": scriptParms };
    
    if (typeof scriptString === 'string' && scriptString.trim().length > 0) {
    } else {
      return result;
    }

    // regex to match the string content from window.watsonAssistantChatOptions = { ...to onLoad:
    const matchRegex = /window\.watsonAssistantChatOptions\s*=\s*\{([\s\S]*?)\s*onLoad:/;
    // match the string
    const match = scriptString.match(matchRegex);
    if (match && match[1]) {
      let optionsString = match[1];
      console.debug("optionString:", optionsString)
      // remove the comments and blanks
      const noCommentsString = removeComments(optionsString)
      console.debug("noCommentsString", noCommentsString)
      // add {} for the string
      const properStartingAndEndingString = ensureProperStartingAndEnding(noCommentsString)
      console.debug("properStartingAndEndingString:", properStartingAndEndingString)
      // remove blanks
      const cleanedString = properStartingAndEndingString.replace(/\n\s*\n/g, '\n').trim();
      console.debug("cleanedString:", cleanedString)
      // the key does not contain "", so we need to add "" to the keys, and add "" to values
      const jsonString = cleanedString.replace(/([{,]\s*)([a-zA-Z0-9_]+)\s*:\s*('([^']*)'|\"([^"]*)\")/g, '$1"$2" : "$4$5"');
      console.debug("jsonString", jsonString);
      try {
        const obj = JSON.parse(jsonString);
        // go through the keys and values
        for (const [key, value] of Object.entries(obj)) {
          scriptParms[key] = value;
        }
      } catch (error) {
        // the format of the script is wrong
        result.invalidText = 'Invalid format of the provided script element. Error when parsing JSON string: ' + error;
        return result;
      }
    } else {
      // the format of the script is wrong
      result.invalidText = 'Invalid format of the provided script element.';
      return result;
    }

    // Validate the scriptParms, should contain "integrationID", "region", "serviceInstanceID"
    if (scriptParms.hasOwnProperty("integrationID") && scriptParms.hasOwnProperty("region") && scriptParms.hasOwnProperty("serviceInstanceID")) {
      if (isValidUUID(scriptParms["integrationID"]) && isValidUUID(scriptParms["serviceInstanceID"])) {
        // if (regions.indexOf(scriptParms["region"]) > -1) {
          //get the src from the scripts
          const regex = /t\.src\s*=\s*("([^"]+)"|'([^']+)')/;
          const matches = scriptString.match(regex);
          if (matches && matches.length > 1) {
            const srcUrl = (matches[2] || matches[3]).trim();
            const webChatScriptPrefix_var = srcUrl.replace(/\/versions\/?$/, '');
            console.debug("webChatScriptPrefix:", webChatScriptPrefix_var);
            serversObj["webChatScriptPrefix"] = webChatScriptPrefix_var;
            if (isHttpOrHttps(webChatScriptPrefix_var)) {
              result.invalid = false;
              return result;
            } else {
              // invalid required src URL
              result.invalidText = 'Invalid format of the provided script element. The src URL is invalid.';
              return result;
            }
          } else {
            // missing required src URL
            result.invalidText = 'Invalid format of the provided script element. The required src URL for loading WatsonAssistantChatEntry.js is missing.';
            return result;
          }
        // } else {
        //   // invalid required region
        //   result.invalidText = 'Invalid format of the provided script element. The region is invalid.';
        //   return result;
        // }
      } else {
        // invalid required integrationID or serviceInstanceID
        result.invalidText = 'Invalid format of the provided script element. The integrationID or serviceInstanceID is invalid.';
        return result;
      }
    } else {
      // missing required parms
      result.invalidText = 'Invalid format of the provided script element. The required integrationID, region, or serviceInstanceID is missing.';
      return result;
    }
  }

  function removeComments(optionsString) {
    const noCommentsString = optionsString.split('\n').map(line => {
      // remove the line begins with '//'
      if (/^\s*\/\//.test(line)) {
        return "";
      }
      // find the index of colon
      const colonIndex = line.indexOf(':');
      if (colonIndex !== -1) {
        // remove the //... outside the single/double quote
        let insideSingleQuote = false;
        let insideDoubleQuote = false;
        let escape = false;
        for (let i = colonIndex; i < line.length; i++) {
            let char = line[i];
            if (escape) {
                escape = false;
                continue;
            }
            if (char === '\\') {
                escape = true;
                continue;
            }
            if (char === '"' && !insideSingleQuote) {
                insideDoubleQuote = !insideDoubleQuote;
            }
            if (char === '\'' && !insideDoubleQuote) {
                insideSingleQuote = !insideSingleQuote;
            }
            if (char === '/' && i + 1 < line.length && line[i + 1] === '/' && !insideSingleQuote && !insideDoubleQuote) {
                return line.substring(0, i).trim();
            }
        }
      } else {
        //if no colon, remove comments
        const commentIndex = line.indexOf('//');
        if (commentIndex !== -1) {
          return line.substring(0, commentIndex).trim();
        }
      }
      // no comments, keep the whole line
      return line.trim();
    }).filter(line => line).join('\n');
    return noCommentsString;
  }

  function ensureProperStartingAndEnding(str) {
    // Trim the string to remove any leading or trailing whitespace
    str = str.trim();
    // Check if the string ends with ','
    if (str.endsWith(',')) {
      str = str.slice(0, -1) + '}';
    } else if (!str.endsWith('}')) {
      // If the string does not end with '}', add it
      str += '}';
    }
    // Check if the string starts with '{'
    if (!str.startsWith('{')) {
      str = '{' + str;
    }
    return str;
  }

  function isValidUUID(uuid) {
    return uuidRegexV4.test(uuid);
  }

  function isHttpOrHttps(url) {
    // match "http" or "https" at the start of the string
    const regex = /^https?:\/\//i;
    return regex.test(url);
  }

  const onChange = useCallback((event) => {
    setUserInputOrign(() => {
      const newUserInputOrign = event.target.value;
      validateUserInput(newUserInputOrign);
      return newUserInputOrign;
    });
  });

  function onRequestSubmit() {
    onStart(userInputObj, userInputOrign);
  };

  function onRequestClose() {
    onCancel();
  };

  const labelText = 'Before you start a web chat, please copy the generated embed script element from IBM watsonx Assistant UI and paste it into below textarea.';
  const helperText = <Link href="https://web-chat.global.assistant.watson.cloud.ibm.com/docs.html?to=api-configuration" target="_blank">Learn more</Link>;
  const placeholder = `<script>
  window.watsonAssistantChatOptions = {
    integrationID: "YOUR_INTEGRATION_ID", // The ID of this integration
    region: "YOUR_REGION", // The region your integration is hosted in, e.g. "us-south", "us-east"
    serviceInstanceID: "YOUR_SERVICE_INSTANCE_ID", // The ID of your service instance
    onLoad: async (instance) => {
      await instance.render();
    }
  };
  setTimeout(function(){
    const t=document.createElement('script');
    t.src="https://YOUR_WEB_CHAT_HOST/versions/" + (window.watsonAssistantChatOptions.clientVersion || 'latest') + "/WatsonAssistantChatEntry.js";
    document.head.appendChild(t);
  });
</script>`;

  return (
    <Modal className={`MyCustomModal ${allowCancel ? "" : "modal-disable-cancel"}`}
      preventCloseOnClickOutside={true}
      open={launchInput}
      modalLabel="Web Chat Integration"
      modalHeading="Web Chat Integration"
      primaryButtonText="Start"
      primaryButtonDisabled={!userInputValid}
      secondaryButtonText="Cancel"
      onRequestSubmit={onRequestSubmit}
      onRequestClose={onRequestClose}>
        {inputError && <InlineNotification hideCloseButton
                          kind="error"
                          subtitle={inputError}
                          title="ERROR: " />}
        <p>{labelText}</p>
        <TextArea
          id="textarea-userInputOrign"
          rows={15}
          labelText="The embed script element:"
          helperText={helperText}
          placeholder={placeholder}
          invalidText={userInputInvalidText}
          invalid={userInputInvalidText != "" ? true : false}
          value={userInputOrign}
          onChange={onChange} />
    </Modal>
  );
}

export { ModalInput };
